import {fireEvent, render, waitFor} from '@testing-library/react-native';
import 'react-native-get-random-values';
import {v4 as uuidv4} from 'uuid';
import Adapter from 'enzyme-adapter-react-16';
import {mount, shallow} from 'enzyme';
import * as enzyme from 'enzyme';
import Base from '../../Apps/Base';
import {loadFeature, defineFeature} from 'jest-cucumber';
import {QuizContext} from './../../Apps/QuizContext';
import {View, TextInput} from 'react-native';
const uuid = uuidv4();
const feature = loadFeature('./features/base.feature');
enzyme.configure({adapter: new Adapter()});
defineFeature(feature, test => {
  const mockQuizContext = {
    score: 7,
    setScore: jest.fn(),
    name: 'chandu',
    setName: jest.fn(),
    email: 'chandra@chandu.com',
    setMail: jest.fn(),
    phone: '1234567890',
    setPhone: jest.fn(),
    selectedValue: 'ReactJS',
    setSelected: jest.fn(),
    trackScore: true,
    setTracker: jest.fn(),
  };

  test('User submits form with invalid email', ({given, when, and, then}) => {
    given('I am on the base screen', async () => {
      let wrapper: any;
      wrapper = shallow(<Base />);
      expect(wrapper.find(View).at(0)).toBeDefind();
    });
    when('I enter an invalid mail {string}', async invalidEmail => {
      let wrapper: any;
      wrapper = shallow(<Base />);
      const email = wrapper.find(TextInput).at(0);
      fireEvent.changeText(email, invalidEmail);
    });
  });
});
